*, :before, :after {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  padding: 0;
}

input, textarea, button {
  font-family: inherit;
}

:root {
  --button-green: #bbf330;
  --dark-green: #7eb101;
  --white: #fefefe;
  --yellow-main: #ffe550;
  --black: #2c2c2c;
  --shadow-fruit: radial-gradient( 50% 50% at 50% 50%, #2f583f 0%, rgba(105, 105, 105, 0) 100% );
  --transition: .4s ease;
}

body {
  color: var(--black);
  height: 100%;
  font-family: Roboto;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
}

.container {
  max-width: 300px;
  margin: 0 auto;
  padding: 0 20px;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-thumb {
  background: var(--dark-green);
  border: 1px solid var(--white);
  border-radius: 20px;
}

body::-webkit-scrollbar-track {
  background-color: var(--yellow-main);
}

@media screen and (min-width: 428px) {
  .container {
    max-width: 396px;
    padding: 0 16px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 16px;
  }
}

.header {
  width: 100%;
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
}

.header:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: var(--dark-green);
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
}

.header-body {
  height: 72px;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

.header-wrap {
  margin-left: auto;
  display: flex;
}

.header-logo {
  z-index: 3;
  width: 66px;
  height: 52px;
  position: relative;
}

.header-logo img {
  width: 100%;
}

.header-order-btn {
  z-index: 3;
  background-color: var(--button-green);
  color: var(--black);
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  margin-left: 30px;
  padding: 6px 16px;
  font-size: 14px;
  display: block;
  position: relative;
}

.header-list {
  z-index: 2;
  gap: 28px;
  display: flex;
  position: relative;
}

.header-link {
  color: var(--black);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

@media screen and (max-width: 767.98px) {
  .header-logo {
    width: 52px;
    height: auto;
  }

  .header-order-btn {
    display: none;
  }

  .header-burger {
    width: 30px;
    height: 20px;
    z-index: 3;
    position: relative;
  }

  .header-body {
    height: 50px;
  }

  .header-burger span {
    content: "";
    width: 100%;
    height: 2px;
    transition: all var(--transition);
    background-color: #fff;
    position: absolute;
    top: 9px;
    left: 0;
  }

  .header-burger:after, .header-burger:before {
    content: "";
    width: 100%;
    height: 2px;
    transition: all var(--transition);
    background-color: #fff;
    position: absolute;
    left: 0;
  }

  .header-burger:before {
    top: 0;
  }

  .header-burger:after {
    bottom: 0;
  }

  .header-burger.burger-active:before {
    top: 9px;
    transform: rotate(45deg);
  }

  .header-burger.burger-active:after {
    bottom: 9px;
    transform: rotate(-45deg);
  }

  .header-burger.burger-active span {
    transform: scale(0);
  }

  .header-menu {
    width: 100%;
    height: 100%;
    background-color: var(--yellow-main);
    transition: all var(--transition);
    padding-top: 150px;
    position: fixed;
    top: 0;
    right: -100%;
  }

  .header-menu.burger-active {
    transition: all var(--transition);
    right: 0;
  }

  .header-list {
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (min-wigth: 768px) {
  .header-burger {
    display: none;
  }
}

@media screen and (min-width: 1280px) {
  .header-order-btn {
    transition: background-color var(--transition);
  }

  .header-order-btn:focus, .header-order-btn:hover {
    background-color: var(--yellow-main);
  }
}



